@import "_style";
// *******************************************
/*start media Query Section*/
/* for mobile & Tablet */
@media (max-width:767px ) {
  p {
    color: red;
    font-size: 22px;
  }

  .header .overlay .plan {
    float: none;
    margin: $sz auto;
  }
}
/* for small screen  */
@media (min-width:768px )and (max-width:991px )
{
  p {
    color: blue;
    font-size: 32px;
  }
  .container {
    width: 750px;
  }
}
/* for medium screen*/
@media (min-width:992px )and (max-width:1199px )
{
  p {
    color: green;
    font-size: 42px;
  }
  .container {
    width: 970px;
  }
}
@media (min-width:1200px )
{
	p {
    color: yellow;
    font-size: 60px;
  }
  .container {
    width: 1170px;
  }
}


// Start radyFRAMEWORK
.container {
  margin: auto;
  padding-left: 15px;
  padding-right: 15px;
}
.txt-cntr {
  text-align: center;
}
// End radyFRAMEWORK
/*end media Query Section*/
